html{
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    margin: 0%;
    font-family: Arial, Helvetica, sans-serif;
    background-color: #0D305F;
    color: #F0F2F5;
    font-size: 1.35em;
    line-height: 1.5;
    text-align: center;
}
img {
    display: block;
    width: 100%;
    height: auto;
    box-shadow:0 15px 25px  rgba(0, 0, 0, .3);
}

.toolbarSpace{
    margin-top: 15%;
}
h1, h2, h3{
    margin: 0%;
    padding: 1em 0;
}
p {
    margin: 0%;
    padding: 1em 0;
}
.btn {
    display: inline-block;
    background: #0D305F;
    color: #F0F2F5;
    text-decoration: none;
    font-size: 1em;
    padding: 0.8em 1.5em;
    border: 0.13em solid #F0F2F5;
    margin: 0.5em 0;
    border-radius: 0.7em;
}
.btn:hover{
    background: #29466E;
    color: #7F8A99;
}
#section-ctres .btn{
    margin-top: 3em;
    margin-bottom: 3em;
}
.red{
    background: red;
    width: 100px;
    height: 100px;
    margin: auto;
    padding: 1em 0;
}

/* header showcase */

#showcase{
    min-height: 700px;
    color: #F0F2F5;
    text-align: center;
}
#showcase .bg-image{
    position: absolute;
    background:  url(/static/media/art4.4d19b36f.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
    max-height: 100%;
    z-index: -1;
    opacity: 0.2;
}
#showcase h1{
    padding-top: 2em;
    padding-bottom: 0%;
}
#showcase .content-wrap,
#showcaseOne .content-wrap,
#section-a .content-wrap{
    padding: 0 1.5em;
}

#showcaseOne{
    min-height: 700px;
    color: #F0F2F5;
    text-align: center;
}
#showcaseOne .bg-image{
    position: absolute;
    background:  url(/static/media/art4.4d19b36f.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: auto;
    opacity: 0.2;
}
#showcaseOne h1{
    padding-top: 1em;
    padding-bottom: 0%;
}
#showcaseOne .content-wrap{
    height: 100%;
    width: auto;
    grid-column: 1;
    display: inline-block;
}
/* section A */

#section-a {
   
    background: rgb(100, 125, 161) ;
    padding-bottom: 2em;
}

/* section B */

#section-b {
    padding: 2em 1em 1em;
}
#section-b ul{
    list-style: none;
    margin: 0%;
    padding: 0%;
}
#section-b li{
    margin-bottom:  1em;
    background: #606E8A;
    color: #EBECF0;
}
.card-content{
    padding: 1.5em;
    box-shadow:0 15px 25px  rgba(0, 0, 0, .4);
}
.card img{
    box-shadow:0 15px 25px  rgba(0, 0, 0, .6);
}

/* section C */

#section-c {
    background: #EBECF0;
    color: #36496D;
    padding: 2em;
}
#section-ca {
    background: rgb(20, 26, 49);
    color: #36496D;
    padding: 2em;
}

#section-cdos {
    background: #4B5E81;
    padding: 2em;
}
#section-ctres{
    background:#737373;
    color: #898989;
    padding: 1.5em;
}
.content-wrap #contactform{
    grid-column: 1;
    display: inline-block;
}

/* section D / Boxes  */
 #section-d .box{
     padding: 2em;
     color: #EBECF0;
 }
 #section-d .box:first-child{
     background: rgb(2, 14, 39);
 }

 /* Footer */

 #main-footer {
     padding: 3em;
     background: black;
     color: #F0F2F5;
     text-align: center;
 }
 #main-footer a{
     color: #889DC8;
     text-decoration: none;
 }

 /* Media Querie */
 @media(max-width: 420px) {
    #showcase h1 {
        
    }
 }
 @media(min-width: 700px) {
     .grid{
         display: grid;
         grid-template-columns: 1fr repeat(2, minmax(auto, 25em)) 1fr;
     }

     #section-a .content-text{
         -webkit-columns: 2;
                 columns: 2;
         grid-column-gap: 2em;
         -webkit-column-gap: 2em;
                 column-gap: 2em;
     }
     #section-a .content-text p{
         padding-top: 0%;
     }

     .content-wrap,
     #section-b ul{
         grid-column: 2/4;
     }
     .box, #main-footer div{
         grid-column: span 2;
     }
     #section-b ul{
         display: flex;
         justify-content: space-around;
     }
     #section-b li{
         width: 31%;
     }
 }
.foto-drawer{
    height: auto;
    background: #DB3343;
    box-shadow: 5px 0px 30px rgba(0, 0, 0, 0.6);
    position: fixed;
    justify-content: space-between;
    align-items: center;
    top: 20%;
    left: 0%;
    width: 55%;
    z-index: 200;
    -webkit-transform: translateX(-200%);
            transform: translateX(-200%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;

}

.center-fit {
    max-width: 100%;
    max-height: 100vh;
    margin: auto;
}
.foto-drawer.open{
    -webkit-transform: translateX(0);
            transform: translateX(0);
    top: 20%;
    left: 0%;
    width: 100%;
    justify-items: center;
    align-items: center;
    height: auto;
}
.fotogrid img{
    border: 2em #DC0808;
    border-radius: 1em;
    margin: auto;
    width: 90%;
    min-height: 70%;
    height: auto;
    justify-content: space-between;
    align-items: center;
}
.fotogrid {
    justify-content: space-between;
}
/* Media Querie */

@media(max-width: 700px) {
    .fotogrid{
        display: grid;
        justify-content: space-between;
        padding: 1em;
    }
  
    .fotogrid img{
        justify-content: space-between;
        display: grid;
        grid-column: 2/4;
        height: auto; 
    }
  

}
@media(min-width: 700px) {
    .fotogrid{
        display: grid;
        justify-content: space-between;
        grid-template-columns: 1fr;
        padding: 0em;
        height: 100%;

    }
    .fotogrid img{
        -webkit-columns: 1;
                columns: 1;
        max-height: 750px;
        padding: 0; 
    }
}
@media(min-width: 950px) {
    .fotogrid img{
        -webkit-columns: 1;
                columns: 1;
        max-height: 750px; 
    }
}
@media (min-width: 1025px ) {
    .foto-drawer.open{
        top: 10%;
        height: 80%;
    }
    .foto-drawer{
        top: 10%;
        left: 0;
        height: 80%;
        width: auto;
    }
    .fotogrid img {
        max-height: 80%;
        width: auto;
        margin-top: auto;
        margin-bottom: auto;
    }
}
@media only screen 
  and (min-device-width: 375px) 
  and (max-device-width: 812px) 
  and (-webkit-min-device-pixel-ratio: 3) { 
    .foto-drawer.open{
        top: 35%;
    }
    .foto-drawer{
        top: 35%;
    }
}
/* ----------- iPhone 5, 5S, 5C and 5SE ----------- */

/* Portrait and Landscape */
@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 568px)
  and (-webkit-min-device-pixel-ratio: 2) {
    .foto-drawer.open{
        top: 35%;
    }
    .fotogrid{
        min-height: 250px;
        max-height: 250px;
    }
    .foto-drawer.open #imgsize{
        position: relative;
        margin: 0;
        height: 100%;
        width: auto;
    }
    .foto-drawer{
        top: 35%;
    }
}
    
.user{
    background-image: url(/static/media/art.b7480d0d.png);
    background-size: cover;
    width: 5em;
    height: 5em;
    border-radius: 0.5em;
    overflow: hidden;
    position: inherit;
    margin: auto;
    margin-top: 0em;
    margin-bottom: 3.5em;
}
.inputBox{
    text-align: center;
}
.inputBox:hover {
    z-index: 1;
}
form{
    font-family: Optima, sans-serif black;
    display: inline-block;
}

.boxContact{
    width: auto;
    padding: 80px;
    background: #EBECF0;
    box-sizing: border-box;
    box-shadow:0 15px 25px  rgba(0, 0, 0, .5);
    border-radius: 10px;
    margin: auto;
    margin-top: 5em;
    margin-bottom: 3em;
}
.boxContactIn{
    width: auto;
    padding: 3.5em;
    background: #999999;
    box-sizing: border-box;
    box-shadow:0 30px 30px rgba(0, 0, 0, .4);
    border-radius: 10px;
    margin: auto;
    margin-top: 3.5em;
}
.boxContact h2{
    margin: 1em 0 2em;
    font-size: 210%;
    padding: 0;
    color: #656464;
    text-align: center; 
}
.boxContact .inputBox{
   align-content: center;
   
}
.boxContact .inputBox input{
    
    width: 80%;
    padding: 10px 0;
    font-size: 16px;
    color: #fff;
    Letter-spacing: 2px;
    margin-bottom: 30px;
    border: none;
    border-bottom: 1px solid #fff ;
    outline: none;
    background: transparent;
}
.boxContact .inputBox label{
   
    padding: 25px 0;
    font-size: 20px;
    color: #656464;
    pointer-events: none;
    -webkit-transition: .5s;
    transition: .5s;
}

.boxContact input[type="submit"]
{
    background: transparent;
    margin-top: 2.5em;
    font-size: 25px;
    border: none;
    outline: none;
    color: #fff;
    background: #656464;
    padding: 25px 75px;
    cursor: pointer;
    border-radius: 5px;
    box-shadow:0 20px 25px rgba(1, 1, 1, .6);
}
.boxContact input[type="submit"]:hover{
    background: #4D5461;
}
@media (max-width: 1025px ) {
    .boxContact{
        padding: 1em
    }
    .boxContact .inputBox label{
        font-size: 22px;
    }
    .boxContact h2{
        font-size: 30px;
        margin: 2em 0 3em;
    }
    .boxContact input[type="submit"]
{
    margin-top: 3em;
    margin-bottom: 2em;
    padding: 20px 70px;
}
}
.toggle-button{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 8vh;
    width: 10vw;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}
.toggle-button:focus{
    outline: none;
}
.toggle-button__line{
    width: 25px;
    height: 5px;
    background: #F0F2F5;
}
.toolbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #DB3343;
    height: 15%;

}
.toolbar__logo{
    width: auto;
    margin-left: 1.2%;
}
.toolbar__logo a{
    color:white;
    text-decoration: none;
    font-size: 150%;
}
.toolbar__img{
    margin-left: 40px;
    width: auto;
    height: 70%;
    border-radius: 0.5em;
}
.large__text{
    margin: 0;
    margin-left: 80px;
    padding-left: 0%;
    font-size: 200%;
}
.redline{
    margin-right: 5%;
}
.toolbar__navigation{
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0% 3%;
    z-index: 2000;
}
#center_img{
    position: relative;
    
}
.toolbar_navigation-items a{
    color: #F0F2F5;
    text-decoration: none;
    padding-left: 0px;

}
.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active{
    color: #DAA4A9;
}
.toolbar_navigation-items ul{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}
.toolbar_navigation-items li{
    padding: 0px 15px;
    font-size: 100%;
}
.toolbar_navigation-items img{
    width: 20%;
    height: 20%;

}

.spacer{
    flex: 1 1;
}

@media (max-width: 1275px) {
    .large__text{
    font-size: 230%;
    }
}
@media ( max-width: 1024px ) {
    .toolbar{
        height: 15%;
    }
    .toolbar_navigation-items{
        display: none;
    }
    .toolbar__logo a{
        font-size: 300%;
        margin: auto;
    }
    .toolbar__img{
        height: 73%;
        margin-left: 27%;
    }
}
@media (min-width: 1025px ) {
    .toolbar__toggle-button{
        display: none;
    }  

}
@media (max-width: 750px) {
    .toolbar__logo a{
        padding-left: 30px;
    }
    
}

.side-drawer{
    height: 100%;
    background: #F0F2F5;
    box-shadow: 5px 0px 30px rgba(0, 0, 0, 0.3);
    position: fixed;
    top: 0%;
    left: 0%;
    width: 35%;
    max-width: 400px;
    z-index: 200;
    -webkit-transform: translateX(-110%);
            transform: translateX(-110%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;

}
.side-drawer.open{
    -webkit-transform: translateX(0);
            transform: translateX(0)
}
.side-drawer ul{
    height: 100%;
    width: 100;
    padding-left: 10%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;

}
.side-drawer a{
    
    font-size: 90%;
    color: #577DC1;
    text-decoration: none;
}
.side-drawer a:hover,
.side-drawer a:active{
    color: rgb(35, 60, 104);
}
.side-drawer li{
    width: 100%;
    margin: 20% 0%;
    flex-direction: column;
    justify-content: center;
}

@media (min-width: 1025px) {
    .side-drawer{
        display: none;
    }
}
@media (max-width: 1024px) {
    .side-drawer {
        width: 28%;
    }
    .side-drawer ul{
        padding: 15% 15%;
    }
    .side-drawer a {
        display: inline-block;
    }
}
@media(min-width: 767px) {
    .side-drawer a{
        font-size: 220%;
    }
}
.backdrop{
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    top: 0;
    z-index: 100;
}
.red{
    grid-column: 1;
    display: inline-block;
    background: #DB3343;
    width: 80%;
    height: auto;
    margin: auto;
    padding: 2em 3em;
    margin: 0;
    border-radius: 1em;
}
.red img{
    margin: 0;
    width: 100%;
    height: auto;
}
